import React, {useState} from "react";
import img from "../../../images/1.jpg";
import {Modal} from '../../Modal/Modal';
import '../../../App.scss';

export const Card = ({card}) => {
  const [modalActive, setModalActive] = useState(false);

  return (
    <>
      <li className="column is-half">
        <div className="card">
          <div className="card_image">
            <img src={`../../../images/${card.image}`} />
            {/*<img src={require(`../../../images/${card.image}`)} alt="f"/>*/}
            {/*<img className="image" src="https://images.unsplash.com/photo-1600588545434-1bcc100f7d58?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1350&q=80" alt=""/>*/}
          </div>
          <div className="card_content">
            <h2 className="card_title">{card.name}</h2>
            <h2 className="card_price">{card.price}</h2>
            <p className="card_text">{(card.description).slice(0, 80)}</p>
            <div className="has-text-centered">
              <button
                onClick={() => {
                  setModalActive(true);
                }}
                className="button is-primary is-outlined"
              >
                Open
              </button>
            </div>
          </div>
        </div>
      </li>

      {modalActive && (
        <Modal
          modalActive={modalActive}
          setModalActive={setModalActive}
          card={card}
        >
          <div className="intro">
            {/*<img className="intro__img" src="https://images.unsplash.com/photo-1600588545434-1bcc100f7d58?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1350&q=80" alt="char image"/>*/}
            <img src={`../../images/${card.image}`} alt=""/>
            <div className="intro__content">
              <p className="intro__name">{card.name}</p>
              <hr/>
              <p className="intro__title">Цена:</p>
              <p>{card.price}</p>
              <p className="intro__title">Описание:</p>
              <p className="intro__description">{card.description}</p>
            </div>
          </div>
        </Modal>
      )}
    </>
  )
}
